<template>
  <div class="header">我的订单</div>
  <div class="container">
    <div v-for="item in orderList" :key="item.shopId" class="container__order">
      <div class="container__order__top">
        <div class="container__order__top__left">{{ item.shopName }}</div>
        <div class="container__order__top__right">已支付</div>
      </div>
      <div class="container__order__bottom">
        <div class="container__order__bottom__left">
          <img
            v-for="imgItem in item.itemList"
            :key="imgItem.id"
            :src="imgItem.imgUrl"
            alt=""
            class="container__order__bottom__left__img"
          />
        </div>
        <div class="container__order__bottom__right">
          <div class="container__order__bottom__right__price">
            ￥{{ computeTotalPriceAndNumber(item.itemList, "price") }}
          </div>
          <div class="container__order__bottom__right__number">
            共{{ computeTotalPriceAndNumber(item.itemList, "number") }}件
          </div>
        </div>
      </div>
    </div>
  </div>
  <docker tag="order" />
</template>
<script>
import Docker from "@/components/Docker.vue";
import { ref } from "@vue/reactivity";
import { get } from "../../utils/request.js";

const useorderListEffect = () => {
  const orderList = ref({});

  const getOrderList = async () => {
    try {
      const result = await get("/order/orderList/1");
      if (result.data.code === "0000") {
        orderList.value = result.data.data;
      } else {
        orderList.value = {};
        console.log("出错了");
      }
    } catch (error) {
      consle.log(error);
    }
  };

  getOrderList();

  const computeTotalPriceAndNumber = (list, flag) => {
    let totalPrice = 0;
    let totalNumber = 0;

    list.forEach((element) => {
      totalPrice += element.count * element.promotionPrice;
      totalNumber += element.count;
    });

    return flag === "price" ? totalPrice.toFixed(2) : totalNumber;
  };

  return { orderList, computeTotalPriceAndNumber };
};

export default {
  name: "orderList",
  components: { Docker },

  setup() {
    const { orderList, computeTotalPriceAndNumber } = useorderListEffect();

    return { orderList, computeTotalPriceAndNumber };
  },
};
</script>
<style lang="scss" scoped>
.header {
  height: 44rem;
  font-family: PingFangSC-Regular;
  font-size: 16rem;
  color: #333333;
  background: #ffffff;
  text-align: center;
  line-height: 44rem;
}

.container {
  position: fixed;
  top: 44rem;
  bottom: 49rem;
  left: 0;
  right: 0;
  background: #f8f8f8;

  &__order {
    width: 339rem;
    height: 110rem;
    margin: 16rem auto;
    padding: 16rem;
    box-sizing: border-box;
    background: #ffffff;
    border-radius: 4rem16rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &__top {
      display: flex;
      justify-content: space-between;
      font-family: PingFangSC-Medium;
      font-weight: 700;

      &__left {
        font-size: 16rem;
        color: #333333;
      }

      &__right {
        font-size: 14rem16rem;
        color: #999999;
      }
    }

    &__bottom {
      display: flex;
      justify-content: space-between;

      &__left {
        &__img {
          width: 40rem;
          height: 40rem;
          margin-right: 12rem;
        }
      }

      &__right {
        font-family: PingFangSC-Regular;
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        &__price {
          font-size: 14rem16rem;
          color: #e93b3b;
        }

        &__number {
          font-size: 12rem16rem;
          color: #333333;
          margin-top: 6rem;
        }
      }
    }
  }
}
</style>
